function CheckForm({ formData }) {
  //  platform_id  env_id 
  if (!formData.platform_id || !formData.env_id) {
    return new Error("platform_id  env_id ");
  }

  //  player_id 0
  if (
    !formData.player_id ||
    !checkNumber(formData.player_id) ||
    parseInt(formData.player_id) <= 0
  ) {
    return new Error("player_id 0");
  }

  //  chipvipnicksexportraitunlockother_idother_value 
  if (
    !formData.chip &&
    !formData.vip &&
    !formData.nick &&
    !formData.sex &&
    !formData.portrait &&
    !formData.unlock &&
    !formData.other_id &&
    !formData.other_value
  ) {
    return new Error(
      "chipvipnicksexportraitunlockother_idother_value "
    );
  }

  //  vip  0~30 
  if (
    formData.vip &&
    (!checkNumber(formData.vip) ||
      parseInt(formData.vip) < 0 ||
      parseInt(formData.vip) > 30)
  ) {
    return new Error("vip  0~30 ");
  }

  //  chip 0
  if (
    !formData.chip ||
    !checkNumber(formData.chip) ||
    parseInt(formData.chip) <= 0
  ) {
    return new Error("chip 0");
  }

  //  other_id 0
  if (
    formData.other_id &&
    (!checkNumber(formData.other_id) || parseInt(formData.other_id) <= 0)
  ) {
    return new Error("other_id 0");
  }

  //  other_value 0 other_id  1  0~30 
  if (
    formData.other_value &&
    (!checkNumber(formData.other_value) || parseInt(formData.other_value) <= 0)
  ) {
    return new Error("other_value 0");
  }
  if (
    parseInt(formData.other_id) === 1 &&
    (parseInt(formData.other_value) < 0 || parseInt(formData.other_value) > 30)
  ) {
    return new Error(" other_id  1 other_value  0~30 ");
  }

  //  other_id  other_value 
  if (
    (formData.other_id && !formData.other_value) ||
    (!formData.other_id && formData.other_value)
  ) {
    return new Error("other_id  other_value ");
  }

  return null; // 
}

function checkNumber(value) {
  return /^\d+$/.test(value);
}

